import React, { useState } from 'react';
import { connect } from 'react-redux';

const ButtonViewMore = ({ className, show, refElement }) => {

    const [click, setClick] = useState(show);

    const showInfo = (click) => {

        if (refElement.current && !click)
            refElement.current.style.display = "none";
        else if (refElement.current && click)
            refElement.current.style.display = "block";
        return true;
    }

    return (
        showInfo(click) &&
        <button
            className={className}
            onClick={() => { setClick(!click); }
            }>
            {click ? "-" : "+"}</button>
    );
};

const mapStateToProps = state => ({
})
const mapDispatchToProps = dispatch => ({
})

export default connect(mapStateToProps, mapDispatchToProps)(ButtonViewMore);