import { MENU } from '../Constants';

export const ACTIONS_MENU = {
    ADD_PAGE: "agregar pagina",
    DELETE_PAGE: "borrar pagina",
    MOVE_PAGE: "mover pagina",
    NEXT_PAGE: "pagina siguiente",
    PREV_PAGE: "pagina anterior",
    RESET_PAGE: "reiniciar paginas",
    CHANGE_PAGE: "cambiar pagina"
}
const menuReducer = (state, action) => {

    if (action.payload.element.name !== MENU) return state;

    let actualPage;

    switch (action.type) {

        case ACTIONS_MENU.ADD_PAGE:

            actualPage = state.actualPage < 0 ? 0 : state.actualPage;

            return {
                ...state,
                pages: [
                    ...state.pages.slice(0,actualPage),
                    {tittle:"Nuevo Titulo",categories:[]},
                    ...state.pages.slice(actualPage)
                ],
                actualPage:actualPage
            };

        case ACTIONS_MENU.DELETE_PAGE:

            actualPage = state.actualPage - (state.actualPage === state.pages.length - 1)
            return {
                ...state,
                pages: state.pages.filter((page, index) => index !== state.actualPage),
                actualPage: actualPage
            };

        case ACTIONS_MENU.MOVE_PAGE:

            return state;

        case ACTIONS_MENU.NEXT_PAGE:

            actualPage = state.actualPage + (state.actualPage < state.pages.length-1);
    
            return {
                ...state,
                actualPage: actualPage
            };

        case ACTIONS_MENU.PREV_PAGE:

            actualPage = state.actualPage - (state.actualPage > 0)
            
            return {
                ...state,
                actualPage: actualPage
            };
        case ACTIONS_MENU.RESET_PAGE:

            return {
                ...state,
                actualPage: 0
            }
        case ACTIONS_MENU.CHANGE_PAGE:
            return {
                ...state,
                actualPage: action.payload.changePage
            }

        default:
            return state;
    }
};

export default menuReducer;