import React, { useState } from 'react'
import { connect } from 'react-redux'
import PageView from './PageView'
// eslint-disable-next-line
//import ButtonDraggPage from '../Buttons/ButtonDraggPage'
//import ButtonChangePage from '../Buttons/ButtonChangePage';

import { ACTIONS_MENU } from '../ReducersComponents/MenuReducer';
import { MENU } from '../Constants';

import '../../styles/menu.scss'
import PageCategories from './PageCategories';

const MenuView = function ({ pages, actualPage, resetPage }) {

    const existsAnyPage = pages && Array.isArray(pages) && pages.length > 0
    const existsPages = existsAnyPage && pages.length > 1;
    const [clicks, setClicks] = useState(0);

    /*
    const changeStylesPageNext = function () {

        setFirstClick(true);

        const $actualPage = document.getElementById('page' + actualPage);
        const numNextPage = actualPage + 1;
        const $nextPage = document.getElementById('page' + numNextPage);

        if ($actualPage && $nextPage) {

            $actualPage.style.transition = "all 0.5s";
            $actualPage.style.transform = "rotateY(-90deg)";
            $actualPage.style.opacity = 0;

            $nextPage.style.transition = "opacity 0.5s";
            $nextPage.style.opacity = 1;
            $nextPage.style.transform = "rotateY(0deg)";

            window.scrollTo({ top: 0, behavior: 'smooth' });
        }
    }

    const changeStylesPagePrev = function () {

        setFirstClick(true);

        const $actualPage = document.getElementById('page' + actualPage);
        const numPrevPage = actualPage - 1;
        const $prevPage = document.getElementById('page' + numPrevPage);

        if ($actualPage && $prevPage) {

            $actualPage.style.transition = "all 0.5s";
            $actualPage.style.opacity = 0;
            $actualPage.style.transform = "rotateY(150deg)";

            $prevPage.style.transition = "all 0.5s";
            $prevPage.style.opacity = 1;
            $prevPage.style.transform = "rotateY(0deg)";

            window.scrollTo({ top: 0, behavior: 'smooth' });
        }
    }
    */

    return (
        <div style={{ position: 'relative' }} className="menu">

            {/*existsPages &&
                <ButtonChangePage
                    changeNextStyles={changeStylesPageNext}
                    changePrevStyles={changeStylesPagePrev}
                />
            */}

            {existsPages && clicks === 1  &&
                <span className="finger-pointing">üëÜüèΩ</span>
            }

            {(!Array.isArray(pages) || pages.length < 1) &&
                <span className="waiting-menu">{"No hay Paginas para mostrar \n"}
                    <strong>(Comuniquelo Por favor)</strong>
                </span>}

            {existsAnyPage &&
                <PageCategories
                    pages={pages}
                    clicks={clicks}
                    setClicks={setClicks}
                    show = {actualPage===0}
                />
            }

            {existsAnyPage &&
                <div className="pages-container">
                    {pages && pages.map((page, indexPage) => (
                        <PageView
                            page={page}
                            indexPage={indexPage+1}
                            show={indexPage+1 === actualPage}
                            maxPages={pages.length}
                            key={"page" + indexPage}
                        >
                        </PageView>
                    ))}
                </div>
            }
            {actualPage !== 0 &&
                <span
                    onClick={() => { resetPage(); if (clicks < 2) setClicks(clicks + 1); }}
                    style={{
                        position: 'fixed',
                        width: '60px',
                        height: '50px',
                        top: '5px',
                        cursor: 'pointer',
                        left: '5px',
                        zIndex: (pages.length + 2) * 10,
                        fontFamily: "Fuente Blest Titulos",
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        fontSize: '40px',
                        borderRadius: '10px',
                        color:'black'
                    }}
                    className="boton-not-pressed">Inicio</span>
            }
        </div>
    )
}
const mapStateToProps = state => ({
    pages: state?.pages,
    actualPage: state?.actualPage
})

const mapDispatchToProps = dispatch => ({
    resetPage() {
        dispatch({
            type: ACTIONS_MENU.RESET_PAGE,
            payload: { element: { name: MENU } }
        });
        window.scrollTo({ top: 0, behavior: 'smooth' });
    }
});

export default connect(mapStateToProps, mapDispatchToProps)(MenuView);