import { MENU, PAGE, CATEGORY, PRODUCT } from '../Constants';

import menuReducer from './MenuReducer';
import pageReducer from './PageReducer';
import categoryReducer from './CategoryReducer';
import productReducer from './ProductReducer';

const NO_CHANGE = -1;

/*Ente encargado de distribuir la porcion del estado que va a cambiar a cada reducerComponent*/


const getCategory = function (initialElement, hierarchicalArray) {
    
    console.log(hierarchicalArray);
    let index = hierarchicalArray.shift();
    let actualCategory = initialElement.categories[index];

    while(actualCategory && actualCategory?.categories && actualCategory?.categories.length>0 && hierarchicalArray.length>0){
        
        console.log(index);
        console.log(actualCategory);
    
        index = hierarchicalArray.shift(); 
        actualCategory = actualCategory.categories[index];
    }

    console.log(actualCategory);
    return actualCategory;
}


const loadCategoryPage = function (pageToLoad, category, hierarchicalArray) {

    let actualCategory = pageToLoad.categories;
    let indexCategory = hierarchicalArray.shift();

    while (actualCategory && hierarchicalArray.length > 0) {

        actualCategory = actualCategory[indexCategory].categories;
        indexCategory = hierarchicalArray.shift();
    }
    actualCategory[indexCategory] = category;
        
}


const globalReducer = (state, action) => {

    const element = action.payload.element;

    if (!element) return state;

    const newPages = element.name !== MENU ? Object.assign([], state.pages) : NO_CHANGE;

    let hierarchicalArray = Object.assign([], element.hierarchicalArray);

    const indexPage = newPages !== NO_CHANGE ? hierarchicalArray.shift() : NO_CHANGE;

    let pageToChange = indexPage !== NO_CHANGE ? newPages[indexPage] : NO_CHANGE;

    let categoryToChange = pageToChange !== NO_CHANGE ?
        getCategory(pageToChange, hierarchicalArray) : NO_CHANGE;

    //console.log(categoryToChange);

    const indexProduct = hierarchicalArray && hierarchicalArray.length > 0 ? hierarchicalArray.shift() : NO_CHANGE;

    let productToChange = indexProduct !== NO_CHANGE ? categoryToChange.products[indexProduct] : NO_CHANGE;


    //console.log(pageToChange);
    //console.log(categoryToChange);
    //console.log(productToChange);

    switch (element.name) {

        case MENU:

            return menuReducer(state, action);

        case PAGE:

            pageToChange = pageReducer(pageToChange, action);
            newPages[indexPage] = pageToChange;
            return {
                ...state,
                pages: newPages
            }

        case CATEGORY:

            categoryToChange = categoryReducer(categoryToChange, action);
            
            hierarchicalArray = Object.assign([],element.hierarchicalArray);
            hierarchicalArray.shift(); //Le quito el primero que se referira a la pagina 

            loadCategoryPage(pageToChange, categoryToChange, hierarchicalArray);

            newPages[indexPage] = pageToChange;

            return {
                ...state,
                pages: newPages
            }

        case PRODUCT:

            productToChange = productReducer(productToChange, action);

            categoryToChange.products[indexProduct] = productToChange;

            hierarchicalArray = Object.assign([],element.hierarchicalArray);
            hierarchicalArray.shift(); //Le quito el primero que se referira a la pagina
            hierarchicalArray.pop(); //Le quito el ultimo que se referira al producto
            
            loadCategoryPage(pageToChange, categoryToChange, hierarchicalArray);

            newPages[indexPage] = pageToChange;

            return {
                ...state,
                pages:newPages
            }

        default:

            return state;
    }

};

export default globalReducer;